body{
    padding:0px;
    margin:0px;
    box-sizing:border-box;
}
#heading{
    height:300px;
    display:flex;
    justify-content: center;
    align-items: center;
    background-color: rgb(80,80,224);

}
h1{
    font-size: 50px;
    color:white;
}
.profile{
    display:flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    margin-left: 30px;
    margin-right:30px;
    margin-top: 30px;

     
}
img{
    height:200px;
    border-radius:50%;
}
.cards{
    text-align:center;
    border:2px black solid;
    margin-top:20px;
    margin-left:20px;
}
.but{
    margin-top:20px;
}
.input{
    margin-left:40vw;
    width:200px;
    height:30px;
    font-size:25px;
    padding-left:1vw;

}
button{
    height:30px;
    background-color: rgb(119,119,224);
    color:white;
    margin-left:1vw;
    
}

